
const contests = {
  title: 'Contents Joined',
  api: '/user_details/user_contests_list',
  columns: [
    {
      name: 'Contest Name',
      minWidth: '100px',
      selector: 'contest_name',
      sortable: true,
      cell: (row) => row.contest_name
    },
    {
      name: 'Type',
      minWidth: '100px',
      selector: 'type',
      sortable: true,
      cell: (row) => row.contest_type
    },
    {
      name: 'Caption',
      minWidth: '100px',
      selector: 'caption',
      sortable: true,
      cell: (row) => row.caption
    },
    {
      name: 'Entry Fee',
      minWidth: '100px',
      selector: 'entry_fee',
      sortable: true,
      cell: (row) => row.entry_fee
    },
    {
      name: 'Total Prize',
      minWidth: '100px',
      selector: 'total_prize',
      sortable: true,
      cell: (row) => row.total_prize
    }
  ]
}

export default {
  contests
}