import React, { Fragment, useEffect } from 'react'
import DataTable from 'react-data-table-component'
import { useRouteMatch } from 'react-router-dom'
import { Card } from 'reactstrap'
import { getUserExtraDetails } from '../../store/actions'
import columnsList from './listColumns'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useDispatch, useSelector } from 'react-redux'

const List = () => {
  const dispatch = useDispatch()
  const match = useRouteMatch()
  const store = useSelector(state => state.users.getTypeList)
  
  const { id, details } = match.params

  useEffect(() => {
    if (columnsList[details]) {
      dispatch(getUserExtraDetails(`${columnsList[details].api}/${id}`))
    }
  }, [])

  if (!columnsList[details]) {
    return <div>URL</div>
  }

  const headerRender = (
    <div className="d-flex justify-content-between">
      <h3>Title is here</h3>
    </div>
  )

  return (
    <Fragment>
      <Card>
        <DataTable
          title={headerRender}
          columns={columnsList[details].columns}
          className="react-dataTable"
          data={store.records}
        />
      </Card>
    </Fragment>
  )
}

export default List
